import React, { useState, useEffect } from 'react';

function Artyesno(props) {
    const [state, setstate] = useState({ yes: 'red', no: 'green' })
    
    useEffect(() => {
        if (props.params === '1'){
           setstate({ yes: 'green', no: 'red' });
    }
        if (props.params === '0'){
           setstate({ yes: 'red', no: 'green' });
    }
    }, [props.params]);
   
    return (
        <div className="col-2 text-center " style = {{float:'right',position:'absolute',marginTop:'-15px',marginLeft:'230px'}}>
            <div >Основная на главной странице</div>
            <div className="row" style = {{cursor:'pointer'}}>
                <div className="col artyes" onClick={() => {  props.setparams('1');}} style={{ backgroundColor: state.yes, color:'#ffffff',paddingTop:'2px', paddingBottom:'2px'}}>Да</div>
                <div className="col artno" onClick={() => { props.setparams('0');}}  style={{ backgroundColor: state.no, color: '#ffffff', paddingTop: '2px', paddingBottom: '2px'}}>Нет</div>
            </div>
        </div>
    )
} export default Artyesno;