import React, { useState, useEffect } from 'react';
import { getFiles, array_obj, initalBaseUrl, deleteFiles } from '../actions';
import Inserticon from '../components/Inserticon';
import Deleteicon from '../components/Deleteicon';
import Filesup from '../components/Filesup';

function Files(props) {
    const [files, setfiles] = useState({ data: {}, status: 0 });
    const [deleteF, setdeleteF] = useState({ data: '', status: 0 });
    const [err, seterr] = useState('');
    const [idfiles, setidfiles] = useState('');
    function filterStr(str = '') {
        if (str){
            seterr(str.split('/')[0] + str.split('/')[4]);
        }
       
   }
    useEffect(() => {
        if (props.location.pathname.split('/')[4] === 'no_files'){
            seterr('Ошибка! Файл не загружен.');
        }
        if (props.location.pathname.split('/')[4] === 'on_files') {
            seterr('Файл успешно загружен.');
        }
        if (props.location.pathname.split('/')[4] === undefined) {
            seterr('');
        }
        
    }, [props.location.pathname.split('/')[4]]);
    useEffect(() => {
        getFiles(setfiles);
        filterStr(deleteF.data);
    }, [deleteF.data, idfiles]);
    useEffect(() => {
        deleteFiles(setdeleteF, idfiles);
        filterStr()
    }, [idfiles]);

    return (
        <div className="col">
            <div className="col row">
               
                <div className="col-2"><h1>Изображение</h1></div>
    
                <div className="col mt-3">
                    <Filesup />
                </div>
                <div className="col mt-3">
                   <h4> {err}</h4>
                </div>
 
            </div>

            <div className="row">

                {array_obj(files.data).map((x, i) =>
                    <div key={i + 10} style={{ border: 'solid 2px green' }} className="form-check m-2 p-0 col-2 text-right " >
                        <div onClick={() => setidfiles(`../../img/window/${x}`)} className = "col p-0 " style = {{position:'absolute',marginTop:'-3px'}} title = "Удалить">
                            <svg style={{ cursor: 'pointer' }} className="bi bi-x-circle-fill " width="1.5em" height="1.5em" viewBox="0 0 16 16" fill="red" xmlns="http://www.w3.org/2000/svg">
                                <path fillRule="evenodd" d="M16 8A8 8 0 110 8a8 8 0 0116 0zm-4.146-3.146a.5.5 0 00-.708-.708L8 7.293 4.854 4.146a.5.5 0 10-.708.708L7.293 8l-3.147 3.146a.5.5 0 00.708.708L8 8.707l3.146 3.147a.5.5 0 00.708-.708L8.707 8l3.147-3.146z" clipRule="evenodd" />
                            </svg>
                        </div>
                        <img width="100%" src={`${initalBaseUrl}/img/window/${x}`} />
                    </div>
                )}
            </div>
        </div>
    )
} export default Files;