import React, { useState, useEffect } from 'react';
import { initalBaseUrl, getPrais, updatePrais } from '../actions';
import { Link } from 'react-router-dom';
import Articles from '../components/Articles';
function Prais(props) {
    const [prais, setprais] = useState({ data: [{}], status: 0 });
    const [updateprais, setupdateprais] = useState({ data: [], status: 0 });
    const [profile, setprofile] = useState('Россия, Турция');
    const [okno, setokno] = useState('Одностворчатое окно');
    const [stvorka, setstvorka] = useState('глухое');
    const [znacenie, setznacenie] = useState(0);
    const [id, setid] = useState(0);
    useEffect(() => {
        getPrais(setprais, profile, okno, stvorka)
    }, [profile, okno, stvorka, updateprais.data]);
    const arrw = [1, 2, 3, 4, 5, 6, 7, 8];
    let m = -9;
    let v = 0;
    let s = 900;
    let w = -100;
    function handleSubmit(event) {
        updatePrais(setupdateprais, znacenie, id);
        event.preventDefault();


    }
    return (
        <div className="container-fluid" >
            {znacenie + ' ' + id}
            <div className="table-responsive">
                <form onSubmit={handleSubmit}>
                    <div className="row">
                        <div className="col">
                            <p><b>Профиль: </b> {(prais.data[0] !== undefined)?prais.data[0].profiles:''}</p>
                            <p><b>Окно: </b>{(prais.data[0] !== undefined) ? prais.data[0].okno:''}</p>
                            <p><b>Створка: </b> {(prais.data[0] !== undefined) ? prais.data[0].stvorka:''}</p>
                        </div>
                        <div className="col">
                            <div className="form-group">
                                <label htmlFor="my-select">Профиль</label>
                                <select onChange={(e) => setprofile(e.target.value)} id="my-select" className="custom-select">
                                    <option>Россия, Турция</option>
                                    <option>3-камерный</option>
                                    <option>5-камерный</option>
                                </select>
                            </div>
                        </div>
                        <div className="col">
                            <div className="form-group">
                                <label htmlFor="my-select">Окно</label>
                                <select onChange={(e) => setokno(e.target.value)} id="my-select" className="custom-select">
                                    <option>Одностворчатое окно</option>
                                    <option>Двустворчатое окно</option>
                                    <option>Трехстворчатое окно</option>
                                </select>
                            </div>
                        </div>
                        <div className="col">
                            <div className="form-group">
                                <label htmlFor="my-select">Створка</label>
                                <select onChange={(e) => setstvorka(e.target.value)} id="my-select" className="custom-select" >
                                    <option value = "глухое">Глухая</option>
                                    <option value="Поворотное">Поворотная</option>
                                    <option value="Поворотно-откидное">Поворотно-откидная</option>
                                </select>
                            </div>
                        </div>
                        <div className="col">

                        </div>
                    </div>
                    <div className="text-center row">
                        <div className = "col"></div>
                        <div className="form-group col-1">
                            <label htmlFor="my-input">Значение</label>
                            <input id="my-input"  value={znacenie} onChange={(e) => setznacenie(e.target.value)} className="form-control" type="text" />
                        </div>
                          <button type="submit" style = {{height:'40px',marginTop:'30px'}} className="btn btn-primary" data-toggle="modal" data-target="#exampleModal">
                                Launch modal
                          </button>
                        <div className="col"></div>
                    </div>

                </form>
                <table className="table mt-5">
                    <thead>
                        <tr>
                            <th scope="row">#</th>
                            {

                                arrw.map((x, i) => <th key={x + 5} scope="col">{s = s + 100}</th>)

                            }
                        </tr>
                    </thead>
                    <tbody>
                        {arrw.map((n, u) => {
                            m = m + 8;
                            v = v + 8;
                            w = w + 100;
                            return (
                                <tr key={n + 50}>
                                    <th scope="row">{(prais.data[u] !== undefined) ? (Number(prais.data[u].width) + w).toString() : ''}</th>
                                    {
                                        prais.data.map((x, i) => {
                                            if (i > m && i < v) { return <td onClick={() => { setznacenie(x.znacenie); setid(x.id)}} className="znacenie" style={{ cursor: 'pointer' }} key={x.id + 50} scope="col">{x.znacenie}</td> }
                                        })

                                    }
                                </tr>)
                        })}


                    </tbody>
                </table>
            </div>

        </div>
    )
}
export default Prais;