import React from 'react';
import axois from 'axios';
import { NavLink } from "react-router-dom";


export const initalBaseUrl = 'http://sandani.local';

export const array_obj = (arr) => {
    let new_arr = [];
    // eslint-disable-next-line
    for (const key in arr) {

        new_arr[key] = arr[key];


    }
    return new_arr;
}

export function getPrais(f, profiles, okno, stvorka) {
    axois.get(initalBaseUrl + '/api/admin/prais.php', {
        params: {
            profiles: profiles,
            okno: okno,
            stvorka: stvorka
        }
    })
        .then(function (response) {
            f({ data: response.data, status: response.status });
        })
}

export function updatePrais(f, znacenie, id) {
    axois.get(initalBaseUrl + '/api/admin/praisupdate.php', {
        params: {
            znacenie: znacenie,
            id: id
        }
    })
        .then(function (response) {
            f({ data: response.data, status: response.status });
        })
}

