export function replaceUrls(str = '') {
    if (str !== undefined) {
        if (str !== 'Главная') {
            str = str.replace(/ /g, '_');

            let a = [];
            let x, y, s;
            let d = [];
            let arr_ru = ['_', 'А', 'а', 'Б', 'б', 'В', 'в', 'Г', 'г', 'Д', 'д', 'Е', 'е', 'Ё', 'ё', 'Ж', 'ж', 'З', 'з', 'И', 'и', 'Й', 'й', 'К', 'к', 'Л', 'л', 'М', 'м', 'Н', 'н', 'О', 'о', 'П', 'п', 'Р', 'р', 'С', 'с', 'Т', 'т', 'У', 'у', 'Ф', 'ф', 'Х', 'х', 'Ц', 'ц', 'Ч', 'ч', 'Ш', 'ш', 'Щ', 'щ', 'Э', 'э', 'Ы', 'ы', 'Ю', 'ю', 'Я', 'я'];

            let arr_en = ['_', 'A', 'a', 'B', 'b', 'V', 'v', 'G', 'g', 'D', 'd', 'E', 'e', 'E', 'e', 'J', 'j', 'Z', 'z', 'I', 'i', 'I', 'i', 'K', 'k', 'L', 'l', 'M', 'm', 'N', 'n', 'O', 'o', 'P', 'p', 'R', 'r', 'S', 's', 'T', 't', 'U', 'u', 'F', 'f', 'H', 'h', 'C', 'c', 'C', 'c', 'H', 'h', 'H', 'h', 'E', 'e', 'I', 'i', 'Y', 'y', 'I', 'i'];
            for (let i = 0; i < str.length; i++) {
                a[i] = str.substr(i, 1)
                x = arr_ru.indexOf(a[i]);
                y = arr_en.indexOf(a[i]);
                d[i] = arr_en[x];
                if (d[i] === undefined) {
                    d[i] = arr_en[y];

                }
            }
            s = d.join('');
            s = s.toLowerCase();
            return s + '.html';
        }
        if (str === 'Главная') {
            return '/';
        }
    }

}