import React from 'react';
import { initalBaseUrl } from '../actions';
function Filesup() {
   
    return (
        <div className="container" >
           
            <form  enctype="multipart/form-data" method="post" action={`${initalBaseUrl}/api/admin/upload.php`}>

                <div className = "row col">
                    <div className="custom-file col">
                    <input type="file" className="custom-file-input"  id="fileupload" name= "userfile" />
                     <label className="custom-file-label" htmlFor="fileupload">Загрузка файла</label>
                    
               </div>
               <div className = "col">
                   <input className="btn btn-primary" type="submit" value="Отправить файл" />
               </div>
                </div>
              
                
           </form>

        </div>
    )
} export default Filesup;