import React, { useState, useEffect } from 'react';
import { deleteArticles, array_obj, initalBaseUrl, getMenuArtId, getArticles, getArticlesId } from '../actions';
import { Link } from 'react-router-dom';
import Deleteicon from '../components/Deleteicon';
import Inserticon from '../components/Inserticon';
function Articlesmain(props) {
    const [articles, setarticles] = useState({ data: [{}], status: 0 });
    const [artalias, setartalias] = useState({ data: [{}], status: 0 });
    const [artdelete, setartdelete] = useState({ data: '', status: 0 });
    const [articlesid, setarticlesid] = useState({ data: [{}], status: 0 });
    const [artarr, setartarr] = useState({ id: 0, c: false });
    const [artarrsd, setartarrsd] = useState([]);
    const [state, setstate] = useState(0);
    const [params, setparams] = useState('');
    useEffect(() => {
        getArticles(setarticles);

    }, [artdelete.data]);
    useEffect(() => {
        getArticlesId(setartalias, props.match.params.id);
        getMenuArtId(setarticlesid, props.match.params.id)
    }, [props.match.params.id]);

    useEffect(() => {
        if (artalias.data.params === '1') {
            setparams('Да');
        } else {
            setparams('Нет');
        }
    }, [props.match.params.id]);

    useEffect(() => {
        const option = artarrsd;
        if (artarr.c === true) {
            option.push(artarr.id);
            setartarrsd(option);
        }
        if (artarr.c === false) {
            var pos = option.indexOf(artarr.id);
            option.splice(pos, 1)
            setartarrsd(option);
        }

    }, [artarr.id, artarr.c]);
    return (
        <div className="container-fluid" >

            <div className="row">
                <div className="col-sm">
                    <table className="table table-light">
                        <thead className="thead-light">
                            <tr>
                                <th style={{ width: '100px' }}></th>
                                <th colSpan="5">
                                    <div className="row">
                                        <div className="col-2">
                                            <h3>Статьи</h3>
                                        </div>
                                        <Inserticon alias={'/admin/index.php/articlesinsert'} title={'Добавить статью'} />
                                        <Deleteicon artdelete={artdelete} deleteArticles={deleteArticles} setartdelete={setartdelete} artarrsd={artarrsd} title={'Удалить статью'} ok={'Статья успешно удалена'} no={'Ошибка! Выберите статью'} />
                                    </div>
                                </th>
                                <th>
                                </th>
                            </tr>
                        </thead>
                        <tbody>
                            {
                                array_obj(articles.data).map((x, i) =>
                                    (artarrsd[i] !== x.art_id) ?
                                        <tr key={x.art_id + 5}>
                                            <td className="form-check text-center" > <input id="my-input" className="form-check-input" type="checkbox" value={x.art_id} onChange={(e) => {
                                                setartarr({ id: e.target.value, c: e.target.checked })
                                            }} /></td>
                                            <td><Link to={`/admin/index.php/articles/${x.art_id}`}>{x.art_name}</Link></td>
                                        </tr>
                                        : '')
                            }
                        </tbody>

                    </table>
                </div>
                <div className="col-sm">
                    <div className="text-right mt-3">
                        <Link to={`/admin/index.php/articlesupdate/${artalias.data.art_id}`} type="button" className="btn btn-primary" data-toggle="modal" data-target="#exampleModal">
                            Редактировать
                     </Link>
                    </div>
                    <div className="row mt-3">
                        <div className="col-3">
                            <h5>Основная</h5>
                        </div>
                        <div className="col">
                            {(artalias.data.params === '1') ? <span>Да</span> : <span>Нет</span>}
                        </div>
                    </div>
                    <div className="row mt-3">
                        <div className="col-3">
                            <h5>Название</h5>
                        </div>
                        <div className="col">
                            {artalias.data.art_name}
                        </div>
                    </div>
                    <div className="row mt-3">
                        <div className="col-3">
                            <h5>Url адрес</h5>
                        </div>
                        <div className="col">
                            {artalias.data.art_alias}
                        </div>
                    </div>
                    <div className="row mt-3">
                        <div className="col-3">
                            <h5>Заголовок страницы</h5>
                        </div>
                        <div className="col">
                            {artalias.data.art_title}
                        </div>
                    </div>
                    <div className="row mt-3">
                        <div className="col-3 ">
                            <h5>Ключевые слова</h5>
                        </div>
                        <div className="col">
                            {artalias.data.art_keyword}
                        </div>
                    </div>
                    <div className="row mt-3">
                        <div className="col-3">
                            <h5>Краткое описание страницы</h5>
                        </div>
                        <div className="col">
                            {artalias.data.art_descript}
                        </div>
                    </div>
                    <div className="row mt-3">
                        <div className="col-3">
                            <h5>Краткое описание статьи</h5>
                        </div>
                        <div className="col">
                            {artalias.data.art_subcontent}
                        </div>
                    </div>
                    {(artalias.data.art_img) ?
                        <div className="row mt-3">
                            <div className="col-3">
                                <h5>Изображение</h5>
                            </div>
                            <div className="col">
                                <img className="img-fluid" src={`${initalBaseUrl}/img/window/${artalias.data.art_img}`} alt={artalias.data.art_name} />
                            </div>
                        </div>
                        : <div></div>
                    }
                    <div className="row mt-3">
                        <div className="col-3">
                            <h5>Текст</h5>
                        </div>
                        <div className="col">
                            {artalias.data.art_content}
                        </div>
                    </div>
                </div>
            </div>

        </div>
    )
}
export default Articlesmain;